<template>
  <div class="admin-uvr">
    <HeaderComp></HeaderComp>
     <div class="container">

      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-secondary">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-secondary">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-secondary">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-secondary">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-light">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-secondary">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>
            
      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>UVR</h1>
          </div>
        </b-card>
      </div>

      <b-alert 
        :show="dismissCountDown" 
        dismissible 
        :variant="message.color" 
        @dismissed="dismissCountDown=0" 
        @dismiss-count-down="countDownChanged" > 
        {{message.text}} 
      </b-alert>


      <div class="edituvr" v-if="edit">
        <h2>Editar un registro de la UVR</h2>
        <form @submit.prevent="editUvr(uvrEditObj)" v-if="edit">
          <input type="text" class="form-control my-2" placeholder="Fecha de vigencia" v-model="uvrEditObj.validdate">
          <input type="text" class="form-control my-2" placeholder="Tasa del día" v-model="uvrEditObj.dayrate">
          <input type="text" class="form-control my-2" placeholder="Tasa del periodo" v-model="uvrEditObj.perrate">
          <b-button variant="outline-primary" type="submit">Editar</b-button>
          <b-button variant="outline-secondary" type="submit" @click="edit=false">Cancelar</b-button>
        </form>
      </div>

      <div class="adduvr" v-if="!edit && admin"><hr>
        <h2>Adicionar un nuevo registro de la UVR</h2><hr>
        <form @submit.prevent="addUvr()" v-if="!edit">
          <input type="text" class="form-control my-2" placeholder="Fecha de vigencia" v-model="uvrObj.validdate">
          <input type="text" class="form-control my-2" placeholder="Tasa del día" v-model="uvrObj.dayrate">
          <input type="text" class="form-control my-2" placeholder="Tasa del periodo" v-model="uvrObj.perrate">
          <b-button variant="outline-success" type="submit">Adicionar</b-button>
        </form>
      </div>

      <div class="listuvr"><hr v-if="admin">
        <h2 v-if="admin">Actualizar y borrar un registro de la UVR</h2><hr v-if="admin">
          <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
           <table class="table table-sm table-bordered table-light" style="width: 100%">  
             <thead>
              <tr>
                <!-- <th scope="col">#</th> -->
                <th scope="col">Fecha de vigencia</th>
                <th scope="col">Tasa del día</th>
                <th scope="col">Tasa del periodo</th>
                <th scope="col" v-if="admin">Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr tr v-for="(item, index) in uvrArray" :key="index">
                <!-- <th scope="row">{{item._id}}</th> -->
                <td>{{item.validdate}}</td>
                <td>{{item.dayrate}}</td>
                <td>{{item.perrate}}</td>
                <td v-if="admin">
                  <button type="button" class="btn btn-success mx-1" @click="activateEdit(item._id)"><i class="fas fa-edit"></i></button>
                  <button type="button" class="btn btn-danger" @click="delUvr(item._id)"><i class="far fa-trash-alt"></i></button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        </div>
     </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminUvr',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  // if (adminAuth!=='ok') {
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      uvrArray:[],
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      uvrObj:{validdate:"", dayrate:"", perrate:""},
      edit:false,
      uvrEditObj:{}
    };
  },

    computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {
    this.listUvr();
  },

  methods: {

    listUvr(){
      this.axios.get('/g-uvrrate')
      .then(res=>{
        // console.log(res.data);
        this.uvrArray=res.data;
        this.uvrArray.splice(0,270);
        this.uvrArray.reverse();
        console.log(this.uvrArray);
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    addUvr(){
        this.axios.post('/p-uvrrate',this.uvrObj)
        .then(res=>{

            this.uvrArray.push(res.data);
            this.uvrArray.reverse();
            this.uvrObj.validdate="";
            this.uvrObj.dayrate="";
            this.uvrObj.perrate="";
            this.mensaje.color="success";
            this.mensaje.texto="Registro adicionado";
            this.showAlert();
        })
        .catch(e=>{
            console.log(e.response);
        })
    },

    activateEdit(id){
      this.edit=true;
      this.axios.get(`/g-uvrrate/${id}`)
      .then(res=>{
          this.uvrEditObj=res.data;
      })
      .catch(e=>{
            console.log(e.response);
      })
    },

    editUvr(editObj){
        this.axios.put(`u-uvrrate/${editObj._id}`, editObj)
      .then(res=>{
        const index= this.uvrArray.findIndex(item=> item._id===res.data._id);
        this.uvrArray[index].validdate=res.data.validdate;
        this.uvrArray[index].dayrate=res.data.dayrate;
        this.uvrArray[index].perrate=res.data.perrate;
        this.mensaje.color="success";
        this.mensaje.texto="Registro editado";
        this.showAlert();
        this.edit=false;
      })
      .catch(e=>{

          console.log(e.response);

      })
    },

    delUvr(id){
      console.log(id)
      this.axios.delete(`d-uvrrate/${id}`) 
      .then(res => { 
        let index = this.uvrArray.findIndex( item => item._id === res.data._id ) 
        this.uvrArray.splice(index, 1); 
        this.message.text = 'Registro Eliminado!'; 
        this.message.color = 'danger'; 
        this.showAlert(); 

        }) 
        .catch( e => { 
          console.log(e.response); 
        })
    },

    listUser(){
      this.axios.get('/g-user/615642f86c7f332aac283669')
      .then(res=>{
        // console.log(res.data);
        this.userArray=res.data;
        // console.log(this.uvrArray.dayrate)
      })
      .catch(e=>{
        console.log(e.response);
      })
    },


    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }

  },
}
</script>

