<template>
  <div class="ser-uvt">
    <HeaderComp></HeaderComp>
      <div class="servicepages" style="margin: 20px auto">
        <router-link to="/service/dolexc"><b-button squared variant="outline-secondary">Dólar</b-button></router-link>
        <router-link to="/service/uvr"><b-button squared variant="outline-secondary">UVR</b-button></router-link>
        <router-link to="/service/uvt"><b-button squared class="text-light">UVT</b-button></router-link>
      </div>

    <b-container fluid="xs">
      <hr>
      <h1>UNIDAD DE VALOR TRIBUTARIO (UVT)</h1>
      <hr>
      <!-- <p style="text-align:justify">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Id, est ipsum consequatur quaerat sed repellat adipisci labore porro itaque distinctio deleniti accusamus vero rem dolore qui iusto nihil in totam!</p> -->
      <div class="card" style="width: 18rem; margin: auto auto">
        <img class="card-img-top" src="../assets/img/uvt_page.png" alt="" style="width: 12rem; margin: auto auto">
        <div class="card-body">
          <h3 class="card-text">Tasa con vigencia <br><br> <span style="color:#abd3d1">{{uvtObj.validyear}}</span></h3><br>
          <h1 class="card-title" style="font-size:3em">$ {{uvtObj.perrate}}</h1>
        </div>
      </div>
      <hr>
      <div class="calc">
        <h2>Calculadora UVT</h2>
        <b-form inline style="margin:0.8em auto">
          <!-- <b-row> -->
          <label class="sr-only" for="inline-form-input-name">Factor de cálculo</label>
          <b-form-input
            id="inline-form-input-name"
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Factor de cálculo"
            type="number"
            v-model="factor"
          ></b-form-input>
          <b-button variant="primary" @click="convUvt(); hide=false">Calcular</b-button>
        <!-- </b-row> -->
        </b-form>
        <h3 id="conversion" v-if="!hide">El valor equivalente en pesos colombianos es de </h3>
        <h2 v-if="!hide">$ {{calcUvt}} </h2>
      </div>
    </b-container>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SerUvt',
  components: {
    HeaderComp,
    FooterComp
  },

  data() {
    return {
      uvtArray:[],
      uvtObj:{validyear:"", inidate:"", enddate: "", resol:"", resoldate:"", perrate:""},
      factor: null,
      calcUvt:null,
      hide: true,
    };
  },

  created() {
    this.getUvt();
  },

  methods: {

    getUvt(){
      this.axios.get('/g-uvtrate')
      .then(res=>{
        this.uvtArray=res.data;
        this.uvtObj=this.uvtArray[this.uvtArray.length-1];
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    convUvt(){
      this.calcUvt=Intl.NumberFormat().format(Math.round(this.factor*this.uvtObj.perrate));
      this.factor=null;
      // return this.factor*this.uvtArray.perrate;
    },
  },
}
</script>

