<script>
export default {
  name: 'HeaderComp',
  props: {
    // msg: String
  },

    data() {
      return {
        authName: window.localStorage.getItem('authName'),
        adminAuth: window.localStorage.getItem('adminAuth'),
        susAuth: window.localStorage.getItem('susAuth'),
      };
    },

    methods: {
    logout(){

      var adminAuth=window.localStorage.getItem('adminAuth');
      if (adminAuth) {
        window.localStorage.removeItem('adminAuth');
        window.localStorage.removeItem('authName');
        window.localStorage.removeItem('admin');

        this.$router.push({path:'/'});
      }
      var susAuth=window.localStorage.getItem('susAuth');
      if (susAuth) {
        window.localStorage.removeItem('susAuth');
        window.localStorage.removeItem('authId');
        window.localStorage.removeItem('authName');
        this.$router.push({path:'/'});
      }
      window.localStorage.removeItem('authName');
    }
  }
}
</script>

