<template>
  <div class="sus-signup">
    <HeaderComp></HeaderComp>
      <div class="content_area">
          <div id="register-card" class="card">
            <div class="card-body">
              <h2 class="text-center">Registro</h2>
              <br>

              <b-alert 
                :show="dismissCountDown" 
                dismissible 
                :variant="mensaje.color" 
                @dismissed="dismissCountDown=0" 
                @dismiss-count-down="countDownChanged" > 
                {{mensaje.texto}} 
              </b-alert>

              <form @submit.prevent="addSuscriber()">
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su nombre" v-model="suscriberObj.name" required>
                </div>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su apellido" v-model="suscriberObj.lastName" required>
                </div>

                <div class="form-group">
                  <input type="email" class="form-control" placeholder="Ingrese su correo" v-model="suscriberObj.email" required>
                </div>
                <div class="form-group">
                  <input type="password" class="form-control" placeholder="Ingrese su contraseña" v-model="suscriberObj.password" required>
                </div>
                <div class="form-group">
                  <input type="tel" class="form-control" placeholder="Ingrese su número de contacto" v-model="suscriberObj.mobile">
                </div>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su país de residencia" v-model="suscriberObj.country">
                </div>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su ciudad de residencia" v-model="suscriberObj.city" required>
                </div>
                <hr>
                <p>¿Qué información desea visualizar en el reporte?</p>

                <b-container class="checkboxes">
                  <b-row>
                    <b-col><label><input type="checkbox" v-model="suscriberObj.divInd">&nbsp; Dólar</label></b-col>
                    <b-col><label><input type="checkbox" v-model="suscriberObj.uvrInd">&nbsp; UVR </label></b-col>
                    <b-col><label><input type="checkbox" v-model="suscriberObj.uvtInd">&nbsp; UVT </label></b-col>
                  </b-row>
                  <b-row>
                    <b-col><label><input type="checkbox" v-model="suscriberObj.habeas" style="margin-top:15px">&nbsp; Acepta el tratamiento de datos personales</label></b-col>
                  </b-row>
                </b-container>
                <hr>
                <b-button variant="primary" type="submit">Registrarse</b-button>     

                <p>Si ya estás registrado. Ingresa <router-link to="/suscriber/login">aquí</router-link></p>

              </form>
            </div>
          </div>
      </div>
      <FooterComp></FooterComp>
    </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SusSignup',
  components: {
    HeaderComp,
    FooterComp
  },
  data(){
    return{
      mensaje: {color: 'success', texto: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      suscriberArray:[],
      suscriberObj:{name:"", lastName:"", email: "", password: "", mobile: "", country: "", city: "", divInd:false, uvrInd:false, uvtInd:false, habeas:false},
    };
  },

  created() {

  },

  methods: {

    addSuscriber(){
      this.axios.post('/p-suscriber/', this.suscriberObj)
      .then(res=>{
      this.suscriberArray.push(res.data);
      this.suscriberObj.name="";
      this.suscriberObj.lastName="";
      this.suscriberObj.email="";
      this.suscriberObj.password="";
      this.suscriberObj.mobile="";
      this.suscriberObj.country="";
      this.suscriberObj.city="";
      this.suscriberObj.divInd=false;
      this.suscriberObj.uvrInd=false;
      this.suscriberObj.uvtInd=false;
      this.suscriberObj.habeas=false;

        // Alerta de mensaje 
        this.mensaje.texto = 'Suscriptor Agregado'; 
        this.mensaje.color = 'success';
        this.showAlert(); 
      })
      .catch(e=>{
        console.log(e.response);
        // Alerta de mensaje 
        this.showAlert(); 
        this.mensaje.color = 'danger'; 
        this.mensaje.texto = e.response.data.error.errors.nombre.message;
      })
      // this.suscriberArray={}
    },
    countDownChanged(dismissCountDown) { 
      this.dismissCountDown = dismissCountDown 
    }, 
    showAlert() { 
      this.dismissCountDown = this.dismissSecs 
    },
  }
};
</script>

<style scoped>

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }

.form-group input{
  width: 20em;
}

.checkboxes{
  margin-top: 5px;
  margin-bottom: 5px;
}

button{
  /* margin-top: 5px; */
  margin-bottom: 10px;
}

</style>