<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SusReport',
  components: {
    HeaderComp,
    FooterComp
  },

beforeCreate() {
  var susAuth=window.localStorage.getItem('susAuth');
  if (!susAuth) {
    this.$router.push({path:'/suscriber/login'});
  }

  var authId=window.localStorage.getItem('authId');
  // console.log(authId);
  this.axios.get('/g-suscriber/'+authId)
      .then(res=>{
        this.suscriberObj=res.data;
        // console.log(this.suscriberObj);
      })
      .catch(e=>{
        console.log(e.response);
      })
},

  data() {
    return {
      dollarexObj:{validdate:"", dayrate:""},
      dollarexArray:[],
      dolGraphArray:[],
      uvrObj:{validdate:"", dayrate:"", perrate:""},
      uvrArray:[],
      uvrGraphArray:[],
      uvtObj:{validyear:"", inidate:"", enddate: "", resol:"", resoldate:"", perrate:""},
      uvtArray:[],
      uvtGraphArray:[],
