      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    showGraphDol(){
      this.axios.get('/g-dollarex')
      .then(res=>{
        this.dolGraphArray=res.data;
        this.dolGraphArray.reverse();
        var data ={
            labels: this.dolGraphArray.map(item => item.validdate),
            datasets:[{
                label: "DÓLAR",
                    data: this.dolGraphArray.map(item => item.dayrate),
                    backgroundColor:'#0066CC',
                    borderColor:'#677'
            }]
        }
        // console.log(data);
        const options ={
            scale:{
                yAxes:[{
                    ticks:{
                        beginAtZero:true
                    }
                }]
            }
        }
        var grafica1 = new Chart('dollar',{type:"line",data}); 
      })
      .catch(e=>{
        console.log(e.response);
      })
