      this.axios.get('/g-uvrrate')
      .then(res=>{
        this.uvrArray=res.data;
        this.uvrObj=this.uvrArray[this.uvrArray.length-1];
        // console.log(this.uvrObj);

      })
      .catch(e=>{
        console.log(e.response);
      })
