      .then(res=>{
        this.uvrGraphArray=res.data;
        // console.log(this.uvrGraphArray);
        this.uvrGraphArray.reverse();
        var data ={
            labels: this.uvrGraphArray.map(item => item.validdate),
            datasets:[{
                label: "UVR",
                    data: this.uvrGraphArray.map(item => item.dayrate),
                    backgroundColor:'#0066CC',
                    borderColor:'#677'
            }]
        }
        // console.log(data);
        const options ={
            scale:{
