      .then(res=>{
        this.uvtArray=res.data;
        this.uvtObj=this.uvtArray[this.uvtArray.length-1];
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    showGraphUvt(){
      this.axios.get('/g-uvtrate')
      .then(res=>{
