<template>
  <div class="admin-login">
    <HeaderComp></HeaderComp>
      <div class="container">

        <b-alert 
          :show="dismissCountDown" 
          dismissible 
          :variant="message.color" 
          @dismissed="dismissCountDown=0" 
          @dismiss-count-down="countDownChanged" > 
          {{message.text}} 
        </b-alert>


          <div id="login-card" class="card">
            <div class="card-body">
              <h2 class="text-center">Ingreso Usuario</h2>
              <br>
              <!-- <form @submit.prevent="loginUser()"> -->
              <form @submit.prevent="loginUser()">
                <div class="form-group">
                  <input type="email" class="form-control" placeholder="Ingrese su correo" v-model="adminLoginObj.email" required>
                </div>
                <div class="form-group">
                  <input type="password" class="form-control" placeholder="Ingrese su contraseña" v-model="adminLoginObj.password" required>
                </div>
                <button type="submit" id="button" class="btn btn-primary deep-purple btn-block ">Ingresar</button>
              </form>
            </div>
          </div>
      </div>
      <FooterComp></FooterComp>
    </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminLogin',

  beforeCreate() {

  },

  components: {
    HeaderComp,
    FooterComp
  },

  beforeCreate() {
    var adminAuth=window.localStorage.getItem('adminAuth');
    if (adminAuth) {
      this.$router.push({path:'/admin/user'});
    }
      var susAuth=window.localStorage.getItem('susAuth');
    if (susAuth) {
      this.$router.push({path:'/suscriber/report'});
    }
  },


    data() {
    return {
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      adminLogArray:[],
      adminLoginObj:{email:"", password:""},
      show: true,
    };
  },

  created() {

  },

  methods: {

    loginUser(){

      this.axios.get('/g-user')
      .then(res=>{
        this.adminLogArray=res.data;
        // console.log(this.adminLogArray);
        this.adminLogArray.forEach((value,index) =>{
          if (this.adminLoginObj.email===value.email) {
            if (this.adminLoginObj.password===value.password) {
              // console.log(this.adminLoginObj.password, value.password)
              window.localStorage.setItem('adminAuth',true);
              window.localStorage.setItem('authName',value.name);
              if (value.profile==="Administrador") {
                window.localStorage.setItem('admin',true);
              }else{
                window.localStorage.setItem('admin',false);
              }
              this.$router.push({path:'/admin/user'});
            }
          }
        });
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }
  }
}
</script>

<style scoped>

button{
  margin-top: 10px;
  margin-bottom: 30px;
}

</style>


