<template>
  <div class="ser-uvr">
    <HeaderComp></HeaderComp>
        <div class="servicepages" style="margin: 20px auto">
          <router-link to="/service/dolexc"><b-button squared variant="outline-secondary">Dólar</b-button></router-link>
          <router-link to="/service/uvr"><b-button squared class="text-light">UVR</b-button></router-link>
          <router-link to="/service/uvt"><b-button squared variant="outline-secondary">UVT</b-button></router-link>
        </div>

    <b-container fluid="xs">
      <hr>
      <h1>UNIDAD DE VALOR REAL (UVR)</h1>
      <hr>
      <!-- <p style="text-align:justify">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Id, est ipsum consequatur quaerat sed repellat adipisci labore porro itaque distinctio deleniti accusamus vero rem dolore qui iusto nihil in totam!</p> -->
      <div class="card" style="width: 18rem; margin: auto auto">
        <img class="card-img-top" src="../assets/img/uvr_page.png" alt="" style="width: 12rem; margin: auto auto">
        <div class="card-body">
          <h3 class="card-text">Tasa con vigencia <br><br><span style="color:#abd3d1"> {{uvrObj.validdate}}</span></h3><br>
          <h1 class="card-title" style="font-size:3em">$ {{uvrObj.dayrate}}</h1>
        </div>
      </div>
      <hr>
      <div class="calc">
        <h2>Calculadora UVR</h2>
        <b-form inline style="margin:0.8em auto">
          <!-- <b-row> -->
          <label class="sr-only" for="inline-form-input-name">Factor de multiplicación</label>
          <b-form-input
            id="inline-form-input-name"
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Factor de cálculo"
            type="number"
            v-model="factor"
          ></b-form-input>
          <b-button variant="primary" @click="convUvr(); hide=false">Calcular</b-button>
        <!-- </b-row> -->
        </b-form>
        <h3 id="conversion" v-if="!hide">El valor equivalente en pesos colombianos es de </h3>
        <h2 v-if="!hide">$ {{calcUvr}}</h2>
      </div>
      </b-container>
      <FooterComp></FooterComp>
    </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SerUvr',
  components: {
    HeaderComp,
    FooterComp
  },
  data() {
    return {
      uvrArray:[],
      uvrObj:{validdate:"", dayrate:"", perrate:""},
      factor: null,
      calcUvr:null,
      hide: true,
    };
  },
  created() {
    this.getUvr();

  },

  methods: {

    getUvr(){
      this.axios.get('/g-uvrrate')
      // this.axios.get('/g-uvrrate/615e39581f8354acc1668190')
      .then(res=>{
        this.uvrArray=res.data;
        this.uvrObj=this.uvrArray[this.uvrArray.length-1];
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    convUvr(){
      this.calcUvr=Intl.NumberFormat().format(Math.round(this.factor*this.uvrObj.dayrate));
      this.factor=null;
      // return this.factor*this.uvrArray.dayrate;
    },
  },

}
</script>

