<template>
  <div class="contact">
    <HeaderComp></HeaderComp>
      <div class="content_area">

        <b-alert 
          :show="dismissCountDown" 
          dismissible 
          :variant="message.color" 
          @dismissed="dismissCountDown=0" 
          @dismiss-count-down="countDownChanged" > 
          {{message.text}} 
        </b-alert>


          <div id="login-card" class="card">
            <div class="card-body">
              <h2 class="text-center">Contáctenos</h2>
              <br>
              <form @submit.prevent="addContact()">
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su nombre" v-model="contactObj.name">
                </div>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese su apellido" v-model="contactObj.lastName">
                </div>
                <div class="form-group">
                  <input type="email" class="form-control" placeholder="Ingrese su correo" v-model="contactObj.email" required>
                </div>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="Ingrese el asunto" v-model="contactObj.subject">
                </div>
                <!-- <div class="form-group"> -->
                  <textarea class="form-control" placeholder="Ingrese el mensaje" v-model="contactObj.message" style="cols=100 rows=8" required></textarea>
                <!-- </div> -->
                <b-button variant="primary" type="submit">Enviar</b-button>
              </form>
            </div>
          </div>      
        </div>
      <FooterComp></FooterComp>
    </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'Contact',
  components: {
    HeaderComp,
    FooterComp
  },

    data() {
    return {
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      contactArray:[],
      contactObj:{name:"", lastName:"", email:"", subject:"", message:""},
    };
  },

    computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {

  },

  methods: {

    addContact(){
        this.axios.post('/p-contact',this.contactObj)
        .then(res=>{

            this.contactArray.push(res.data);
            this.contactObj.name="";
            this.contactObj.lastName="";
            this.contactObj.email="";
            this.contactObj.subject="";
            this.contactObj.message="";
            this.mensaje.color="success";
            this.mensaje.texto="Registro adicionado";
            this.showAlert();
        })
        .catch(e=>{
            console.log(e.response);
        })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }
  },
}
</script>

<style scoped>

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }

.form-group input{
  width: 20em;
}

</style>

