  <div class="ser-dolexc">
    <HeaderComp></HeaderComp>
        <div class="servicepages" style="margin: 20px auto">
          <router-link to="/service/dolexc"><b-button squared class="text-light">Dólar</b-button></router-link>
          <router-link to="/service/uvr"><b-button squared variant="outline-secondary">UVR</b-button></router-link>
          <router-link to="/service/uvt"><b-button squared variant="outline-secondary">UVT</b-button></router-link>
        </div>

    <b-container fluid="xs">
      <hr>
      <h1>DÓLAR</h1>
      <hr>
      <!-- <p style="text-align:justify">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Id, est ipsum consequatur quaerat sed repellat adipisci labore porro itaque distinctio deleniti accusamus vero rem dolore qui iusto nihil in totam!</p> -->
      <div class="card" style="width: 18rem; margin: auto auto">
        <img class="card-img-top" src="../assets/img/exchange_page.png" alt="" style="width: 12rem; margin: auto auto">
        <div class="card-body">
          <h3 class="card-text">Tasa con vigencia <br><br><span style="color:#abd3d1">{{dollarexObj.validdate}}</span></h3><br>
          <!-- <h1 class="card-title" style="font-size:3em">$ {{rateFormat}}</h1> -->
          <h1 class="card-title" style="font-size:3em">$ {{dollarexObj.dayrate}}</h1>
        </div>
      </div>
      <hr>
      <div class="calc">
        <h2>Conversor</h2>
        <h3>Pesos Colombianos - Dólares Estadounidenses</h3>
        <b-form inline style="margin:0.8em auto">
          <!-- <b-row> -->
          <label class="sr-only" for="inline-form-input-name">Cantidad en pesos colombianos a convertir</label>
          <b-form-input
            id="inline-form-input-name"
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Pesos Colombianos"
            type="number"
            v-model="pesos"
            required
          ></b-form-input>
          <b-button variant="primary" @click="convPesDol(); hide=false">Convertir</b-button>
        <!-- </b-row> -->
        </b-form>
        <h3 id="conversion" v-if="!hide">Cantidad en Dólares Estadounidenses</h3>
        <!-- <h2>$ {{convPesDol()}} </h2> -->
        <h2 v-if="!hide">$ {{calcPesDol}}</h2>
      </div>
      <hr>

      <div class="calc">
        <h2>Conversor</h2>
        <h3>Dólares Estadounidenses - Pesos Colombianos</h3>
        <b-form inline style="margin:0.8em auto">
          <!-- <b-row> -->
          <label class="sr-only" for="inline-form-input-name">Cantidad en dólares estadounidenses a convertir</label>
          <b-form-input
            id="inline-form-input-name"
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Dólares estadounidenses"
            type="number"
            v-model="dollars"
            required
          ></b-form-input>
          <b-button variant="primary" @click="convDolPes(); hide=false">Convertir</b-button>
        <!-- </b-row> -->
        </b-form>
        <h3 id="conversion" v-if="!hide">Cantidad en Pesos Colombianos </h3>
        <h2 v-if="!hide">$ {{calcDolPes}}</h2>
