      </div>
      </b-container>
      <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SerDolExc',
  components: {
    HeaderComp,
    FooterComp
  },

  data() {
    return {
      dollarexArray:[],
      dollarexObj:{validyear:"", dayrate:""},
      pesos: null,
      dollars:null,
      calcPesDol: null,
      calcDolPes: null,
      hide: true,
    };
  },
  created() {
    this.getDolarEx();
  },

  methods: {

    getDolarEx(){
      // this.axios.get('/g-dollarex/615e1241d9ba568b8d50c0bb')
      this.axios.get('/g-dollarex')
      .then(res=>{
        this.dollarexArray=res.data;
        this.dollarexObj=this.dollarexArray[this.dollarexArray.length-1];
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    convPesDol(){
      this.calcPesDol= (this.pesos/this.dollarexObj.dayrate).toFixed(2);
      this.pesos=null;
    },

    convDolPes(){
      this.calcDolPes=Intl.NumberFormat().format(Math.round(this.dollars*this.dollarexObj.dayrate));
      this.dollars=null;
    },

  },
}
</script>

