<template>
  <div class="admin-uvt">
    <HeaderComp></HeaderComp>
     <div class="container">

      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-secondary">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-secondary">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-secondary">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-secondary">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-secondary">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-light">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>  

      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>UVT</h1>
          </div>
        </b-card>
      </div>

      <b-alert 
        :show="dismissCountDown" 
        dismissible 
        :variant="message.color" 
        @dismissed="dismissCountDown=0" 
        @dismiss-count-down="countDownChanged" > 
        {{message.text}} 
      </b-alert>

      <div class="edituvt" v-if="edit"><hr>
        <h2>Editar un registro de la UVT</h2><hr>
          <form @submit.prevent="editUvt(uvtEditObj)" v-if="edit">
            <input type="text" class="form-control my-2" placeholder="Año de vigencia" v-model="uvtEditObj.validyear">
            <input type="text" class="form-control my-2" placeholder="Fecha de inicio" v-model="uvtEditObj.inidate">
            <input type="text" class="form-control my-2" placeholder="Fecha de fin" v-model="uvtEditObj.enddate">
            <input type="text" class="form-control my-2" placeholder="Resolución" v-model="uvtEditObj.resol">
            <input type="text" class="form-control my-2" placeholder="Fecha de Resolución" v-model="uvtEditObj.resoldate">
            <input type="text" class="form-control my-2" placeholder="Tasa de periodo" v-model="uvtEditObj.perrate">
            <b-button variant="outline-primary" type="submit">Editar</b-button>
            <b-button variant="outline-secondary" type="submit" @click="edit=false">Cancelar</b-button>
          </form>
        </div>

        <div class="adddol" v-if="!edit && admin"><hr>
          <h2>Adicionar un nuevo registro de la UVT</h2><hr>
          <form @submit.prevent="addUvt()" v-if="!edit">
            <input type="text" class="form-control my-2" placeholder="Año de vigencia" v-model="uvtObj.validyear">
            <input type="text" class="form-control my-2" placeholder="Fecha de inicio" v-model="uvtObj.inidate">
            <input type="text" class="form-control my-2" placeholder="Fecha de fin" v-model="uvtObj.enddate">
            <input type="text" class="form-control my-2" placeholder="Resolución" v-model="uvtObj.resol">
            <input type="text" class="form-control my-2" placeholder="Fecha de Resolución" v-model="uvtObj.resoldate">
            <input type="text" class="form-control my-2" placeholder="Tasa de periodo" v-model="uvtObj.perrate">
            <b-button variant="outline-success" type="submit">Adicionar</b-button>
          </form>
        </div>

        <div class="listuvt"><hr v-if="admin">
          <h2 v-if="admin">Actualizar y borrar un registro de la UVT</h2><hr v-if="admin">
          <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
            <table class="table table-sm table-bordered table-light" style="width: 100%">  
            <thead>
              <tr>
                <!-- <th scope="col">#</th> -->
                <th scope="col">Año de vigencia</th>
                <th scope="col">Fecha de inicio</th>
                <th scope="col">Fecha de fin</th>
                <th scope="col">Resolución</th>
                <th scope="col">Fecha de Resolución</th>
                <th scope="col">Tasa de periodo</th>
                <th scope="col" v-if="admin">Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in uvtArray" :key="index">
                <!-- <th scope="row">{{item._id}}</th> -->
                <td>{{item.validyear}}</td>
                <td>{{item.inidate}}</td>
                <td>{{item.enddate}}</td>
                <td>{{item.resol}}</td>
                <td>{{item.resoldate}}</td>
                <td>{{item.perrate}}</td>
                <td v-if="admin">
                  <button type="button" class="btn btn-success mx-1" @click="activateEdit(item._id)"><i class="fas fa-edit"></i></button>
                  <button type="button" class="btn btn-danger" @click="delUvt(item._id)"><i class="far fa-trash-alt"></i></button>
                </td>
              </tr>
            </tbody>
          </table>
         </div>
        </div>
      </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminUvt',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  // if (adminAuth!=='ok') {
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      uvtArray:[],
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      uvtObj:{validyear:"", inidate:"", enddate: "", resol:"", resoldate:"", perrate:""},
      edit:false,
      uvtEditObj:{}
    };
  },

    computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {
    this.listUvt();
  },

  methods: {

    listUvt(){
      this.axios.get('/g-uvtrate')
      .then(res=>{
        this.uvtArray=res.data;
        this.uvtArray.reverse();
        // console.log(this.uvtArray.validyear)
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    addUvt(){
        this.axios.post('/p-uvtrate',this.uvtObj)
        .then(res=>{

            this.uvtArray.push(res.data);
            this.uvtArray.reverse();
            this.uvtObj.validyear="";
            this.uvtObj.inidate="";
            this.uvtObj.enddate="";
            this.uvtObj.resol="";
            this.uvtObj.resoldate="";
            this.uvtObj.perrate="";
            this.mensaje.color="success";
            this.mensaje.texto="Registro adicionado";
            this.showAlert();
        })
        .catch(e=>{
            console.log(e.response);
        })
    },

    activateEdit(id){
      this.edit=true;
      this.axios.get(`/g-uvtrate/${id}`)
      .then(res=>{
          this.uvtEditObj=res.data;
      })
      .catch(e=>{
            console.log(e.response);
      })
    },

    editUvt(editObj){
        this.axios.put(`u-uvtrate/${editObj._id}`, editObj)
      .then(res=>{
        const index= this.uvtArray.findIndex(item=> item._id===res.data._id);
        this.uvtArray[index].validyear=res.data.validyear;
        this.uvtArray[index].inidate=res.data.inidate;
        this.uvtArray[index].enddate=res.data.enddate;
        this.uvtArray[index].resol=res.data.resol;
        this.uvtArray[index].resoldate=res.data.resoldate;
        this.uvtArray[index].perrate=res.data.perrate;
        this.mensaje.color="success";
        this.mensaje.texto="Registro editado";
        this.showAlert();
        this.edit=false;
      })
      .catch(e=>{

          console.log(e.response);

      })
    },

    delUvt(id){
      console.log(id)
      this.axios.delete(`d-uvtrate/${id}`) 
      .then(res => { 
        let index = this.uvtArray.findIndex( item => item._id === res.data._id ) 
        this.uvtArray.splice(index, 1); 
        this.message.text = 'Registro Eliminado!'; 
        this.message.color = 'danger'; 
        this.showAlert(); 

        }) 
        .catch( e => { 
          console.log(e.response); 
        })
    },

    listUser(){
      this.axios.get('/g-user/615642f86c7f332aac283669')
      .then(res=>{
        // console.log(res.data);
        this.userArray=res.data;
        // console.log(this.uvtArray.validyear)
      })
      .catch(e=>{
        console.log(e.response);
      })
    },


    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }

  },
}
</script>

