<template>
  <div class="admin-sus">
    <HeaderComp></HeaderComp>
     <div class="container">

      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-secondary">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-light">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-secondary">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-secondary">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-secondary">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-secondary">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>

      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>SUSCRIPTOR</h1>
          </div>
        </b-card>
      </div>

      <b-alert
          :show="dismissCountDown"
          dismissible
          :variant="mensaje.color"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged">
      {{mensaje.texto}}
      </b-alert>

      <div class="addsus" v-if="!agregar && admin">
        <h2>Adicionar un nuevo registro de Suscriptor</h2><hr>       
        <form @submit.prevent="addSuscriber()">
          <div class="field-group">
            <!-- <input type="text" placeholder="Nombre" v-model="userArray.name">
            <input type="text" placeholder="Apellido" v-model="userArray.lastname">
            <input type="email" placeholder="Email" v-model="userArray.email">
            <input type="password" placeholder="Password" v-model="userArray.password">
            <input type="text" placeholder="Celular" v-model="userArray.mobile">
            <input type="text" placeholder="Pais" v-model="userArray.country">
            <input type="text" placeholder="Ciudad" v-model="userArray.city"> -->

          <input type="text" class="form-control my-2" placeholder="Nombre" v-model="userArray.name">
          <input type="text" class="form-control my-2" placeholder="Apellido" v-model="userArray.lastname">
          <input type="email" class="form-control my-2" placeholder="Email" v-model="userArray.email">
          <input type="password" class="form-control my-2" placeholder="Password" v-model="userArray.password">
          <input type="text" class="form-control my-2" placeholder="Celular" v-model="userArray.mobile">
          <input type="text" class="form-control my-2" placeholder="Pais" v-model="userArray.country">
          <input type="text" class="form-control my-2" placeholder="Ciudad" v-model="userArray.city">
          </div>
          <b-container class="checkboxes">
            <b-row>
              <b-col style="width:10em"><label><input type="checkbox" v-model="userArray.divInd">&nbsp; Dólar</label></b-col>
              <b-col><label><input type="checkbox" v-model="userArray.uvrInd">&nbsp; UVR </label></b-col>
              <b-col><label><input type="checkbox" v-model="userArray.uvtInd">&nbsp; UVT </label></b-col>
            </b-row>
            <b-row>
              <b-col><label><input type="checkbox" v-model="userArray.habeas" style="margin-top:15px">&nbsp; Acepta el tratamiento de datos personales</label></b-col>
            </b-row>
          </b-container>


          <!-- <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox"  value="" id="inlineCheckbox1" v-model="userArray.divInd">
             <label class="form-check-label" for="inlineCheckbox2">Dólar</label>
          </div>          
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox2"  v-model.number="userArray.uvrInd">
             <label class="form-check-label" for="inlineCheckbox2">UVR</label>
          </div>          
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox3"  v-model.number="userArray.uvtInd">
             <label class="form-check-label" for="inlineCheckbox2">UVT</label>
          </div>          
          <div class="form-check">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox4"  v-model.number="userArray.habeas">
             <label class="form-check-label" for="inlineCheckbox2">Acepta el tratamiento de datos personales</label>
          </div> -->
          <div class="col text-center">
              <button type="submit" id="button" class="btn btn-primary deep-purple btn-block">Adicionar</button>          
          </div>
        </form>
      </div>

      <div class="editsus" v-if="agregar"><hr>
        <h2>Editar un registro de Suscriptor</h2><hr>
        <form @submit.prevent="editUser(userEdit)">
          <input type="text" class="form-control my-2" placeholder="Nombre" v-model="userEdit.name">
          <input type="text" class="form-control my-2" placeholder="Apellido" v-model="userEdit.lastname">
          <input type="email" class="form-control my-2" placeholder="Email" v-model="userEdit.email">
          <input type="password" class="form-control my-2" placeholder="Password" v-model="userEdit.password">
          <input type="text" class="form-control my-2" placeholder="Celular" v-model="userEdit.mobile">
          <input type="text" class="form-control my-2" placeholder="Pais" v-model="userEdit.country">
          <input type="text" class="form-control my-2" placeholder="Ciudad" v-model="userEdit.city">
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox1"  v-model="userEdit.divInd">
             <label class="form-check-label" for="inlineCheckbox2">Indicador de divisas</label>
          </div>          
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox2"  v-model="userEdit.uvrInd">
             <label class="form-check-label" for="inlineCheckbox2">Indicador UVR</label>
          </div>          
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox3"  v-model="userEdit.uvtInd">
             <label class="form-check-label" for="inlineCheckbox2">Indicador UVT</label>
          </div>          
          <div class="form-check form-check-inline">
             <input class="form-check-input" type="checkbox" id="inlineCheckbox4"  v-model="userEdit.habeas">
             <label class="form-check-label" for="inlineCheckbox2">Habeas Data</label>
          </div>
          <div class="col text-center">
              <b-button class="btn btn-primary deep-purple btn-block mx-2" type="submit">Actualizar</b-button>
              <button type="reset" id="button" class="btn btn-primary deep-purple btn-block" @click="agregar=false">Cancelar</button>
          </div>
        </form>
      </div>

        <hr v-if="admin"><h2 v-if="admin">Actualizar y borrar un registro del Dólar</h2><hr v-if="admin">
        <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
           <table class="table table-sm table-bordered table-light" style="width: 100%">  
            <thead>
              <tr>
                <th scope="col" style="display:none;">#</th>
                <th scope="col">Nombre</th>
                <th scope="col">Apellido</th>
                <th scope="col">Correo</th>
                <th scope="col">Número de contacto</th>
                <th scope="col">Ciudad</th>
                <th scope="col">Ind. Divisas</th>
                <th scope="col">Ind. UVR</th>
                <th scope="col">Ind. UVT</th>
                <th scope="col">Habeas Data</th>
                <th scope="col" v-if="admin">Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in suscriberArray" :key="index">
                <th scope="row" style="display:none;">{{item._id}}</th>
                <td>{{item.name}}</td>
                <td>{{item.lastname}}</td>
                <td colspan="1">{{item.email}}</td>
                <td colspan="1">{{item.mobile}}</td>
                <td colspan="1">{{item.city}}</td>
                <td colspan="1">{{item.divInd}}</td>
                <td>{{item.uvrInd}}</td>
                <td>{{item.uvtInd}}</td>
                <td>{{item.habeas}}</td>
                <td v-if="admin">
                  <button type="button" class="btn btn-success mx-1" @click="editSuscriptor(item._id)"><i class="fas fa-edit"></i></button>
                  <button type="button" class="btn btn-danger" @click="delSuscriptor(item._id)"><i class="far fa-trash-alt"></i></button>
                </td>
              </tr>
            </tbody>
         </table>
       </div>
     </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'



export default {
  name: 'AdminSus',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      suscriberArray:[],
      userArray:{name:"",lastname:"",email:"",password:"",mobile:"",country:"",city:"",divInd:"false",uvrInd:"false",uvtInd:"false",habeas:"false"},
      agregar: false,
      userEdit:{},
      mensaje: {color: 'success', texto: ''},
      dismissSecs: 5,
      dismissCountDown: 0     
    };
  },

    computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {
    this.listSuscriber();
    this.addSuscriber();
    // this.editUser();
    // this.delSuscriptor();
  },
 mounted: function() {
        this.askName() // Calls the method before page loads
  },
  methods: {
      // Declares the method
    askName: function(){
        this.listSuscriber()
    },
     countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
    },
    showAlert() {
       this.dismissCountDown = this.dismissSecs
    },
    addSuscriber(){
      this.axios.post("/p-suscriber",this.userArray)
      .then(response=>{
        // Agrega al final de nuestro array
        //this.suscriberArray.push(response.data);

        this.suscriberArray.unshift(response.data);
        this.userArray.name="";
        this.userArray.lastname="";
        this.userArray.email="";
        this.userArray.password="";
        this.userArray.mobile="";
        this.userArray.country="";
        this.userArray.city="";
        this.userArray.divInd=false;
        this.userArray.uvrInd=false;
        this.userArray.uvtInd=false;
        this.userArray.habeas=false;
        this.mensaje.texto = 'Suscriptor Adicionado'
        this.mensaje.color = 'success'
        this.showAlert();
      })
      .catch(e=>{
         console.log(e.response);
      })
    },
    listSuscriber(){
      this.axios.get('/g-suscriber')
      .then(res=>{
        // console.log(res.data);
        this.suscriberArray=res.data;
        // console.log(this.uvtArray.validyear)
      })
      .catch(e=>{
        console.log(e.response);
      })
    },
    editSuscriptor(id){
        this.agregar = true;
        this.axios.get(`/g-suscriber/${id}`)
        .then(res => {
            this.userEdit = res.data;
        })
        .catch(e => {
           console.log(e.response);
        })
    },
    editUser(item){
        this.axios.put(`u-suscriber/${item._id}`, item)
        .then(res => {
            let index = this.suscriberArray.findIndex( item => item._id === this.userEdit._id);
            this.suscriberArray[index].name = this.userEdit.name;
            this.suscriberArray[index].lastname = this.userEdit.lastname;
            this.suscriberArray[index].email = this.userEdit.email;
            this.suscriberArray[index].password = this.userEdit.password;
            this.suscriberArray[index].mobile = this.userEdit.mobile;
            this.suscriberArray[index].country = this.userEdit.country;
            this.suscriberArray[index].city = this.userEdit.city;
            this.suscriberArray[index].divInd = this.userEdit.divInd;
            this.suscriberArray[index].uvrInd = this.userEdit.uvrInd;
            this.suscriberArray[index].uvtInd = this.userEdit.uvtInd;
            this.suscriberArray[index].habeas = this.userEdit.habeas;
            this.userEdit = {};
            this.mensaje.texto = 'Suscriptor Actualizado';
            this.mensaje.color = 'success';
            this.showAlert();
        })
        .catch(e => {
            console.log(e);
        })
        this.agregar = false;
    },
    delSuscriptor(id){
      this.axios.delete(`d-suscriber/${id}`) 
      .then(res => { 
        let index = this.suscriberArray.findIndex( item => item._id === res.data._id ) 
        this.suscriberArray.splice(index, 1); 
        this.mensaje.texto = 'Suscriptor Eliminado!'; 
        this.mensaje.color = 'danger'; 
        this.showAlert();
        }) 
        .catch( e => { 
          console.log(e.response); 

        })
    },
  },
}
</script>

<style scoped>

h2{
  color: #467694;
  text-align: center;
}

.mt-3 button{
  background: white;
  border: 2px solid #467694;
  width: 7em;
}

.mt-3 .butsus{
  background:#467694;
}

.card{
  margin-top: 15px;
  margin-bottom: 15px;
}

.field-group{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }

.form-control{
  width: 20em;
} 
.form-check-inline{
  justify-content: center;
  align-items: center;
}

.checkboxes{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80em;
}

.ocultar {
        display: none;
    }
    .mostrar {
        display: block;
}

table{
  margin-top: 40px;
  align-items: center;
  text-align: center;
  width: 30em;
}
.my-custom-scrollbar {
      position: relative;
      height: 200px;
      overflow: auto;
}
.table-wrapper-scroll-y {
      display: block;
}
.table-image 
  td, th {
    vertical-align: middle;
}

</style>
