<template>
  <div class="sus-login">
    <HeaderComp></HeaderComp>
      <div class="container">

        <b-alert 
          :show="dismissCountDown" 
          dismissible 
          :variant="message.color" 
          @dismissed="dismissCountDown=0" 
          @dismiss-count-down="countDownChanged" > 
          {{message.text}} 
        </b-alert>


          <div id="login-card" class="card">
            <div class="card-body">
              <h2 class="text-center">Ingreso Suscriptor</h2>
              <br>
              <form @submit.prevent="loginSus()">
                <div class="form-group">
                  <input type="email" class="form-control" placeholder="Ingrese su correo" v-model="SusLoginObj.email" required>
                </div>
                <div class="form-group">
                  <input type="password" class="form-control" placeholder="Ingrese su contraseña" v-model="SusLoginObj.password" required>
                </div><br>
                <button type="submit" id="button" class="btn btn-primary deep-purple btn-block ">Ingresar</button>

                <p>Si aun no se ha registrado. Ingrese <router-link to="/suscriber/signup">aquí</router-link></p>
              </form>
            </div>
          </div>
      </div>
      <FooterComp></FooterComp>
    </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'SusLogin',
  components: {
    HeaderComp,
    FooterComp
  },

  beforeCreate() {
    var adminAuth=window.localStorage.getItem('adminAuth');
    if (adminAuth) {
      this.$router.push({path:'/admin/user'});
    }
      var susAuth=window.localStorage.getItem('susAuth');
    if (susAuth) {
      this.$router.push({path:'/suscriber/report'});
    }
},


    data() {
    return {
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      SusLoginArray:[],
      SusLoginObj:{email:"", password:""},
      // show: true,
    };
  },

  created() {
    // this.addSuscriber();
  },

  methods: {

    loginSus(){

      this.axios.get('/g-suscriber')
      .then(res=>{
        this.SusLoginArray=res.data;
        // console.log(this.adminLogArray);
        this.SusLoginArray.forEach((value,index) =>{
          if (this.SusLoginObj.email===value.email) {
            if (this.SusLoginObj.password===value.password) {
              // console.log(this.SusLoginObj.password, value.password)
              window.localStorage.setItem('susAuth',true);
              window.localStorage.setItem('authId',value._id);
              window.localStorage.setItem('authName',value.name);
              this.$router.push({path:'/suscriber/report'});
            }
          }
        });
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }

  }
}
</script>

<style scoped>

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }

.form-group input{
  width: 20em;
}

button{
  margin-top: 10px;
  margin-bottom: 10px;
}

</style>