<template>
  <div class="admin-pqr">
    <HeaderComp></HeaderComp>
    <div class="container">

      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-secondary">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-secondary">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-light">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-secondary">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-secondary">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-secondary">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>  

      <!-- <h1>UVT</h1> -->
      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>CONTÁCTENOS</h1>
          </div>
        </b-card>
      </div>

      <b-alert 
        :show="dismissCountDown" 
        dismissible 
        :variant="message.color" 
        @dismissed="dismissCountDown=0" 
        @dismiss-count-down="countDownChanged" > 
        {{message.text}} 
      </b-alert>

      <div class="listpqr"><hr v-if="admin">
        <h2 v-if="admin">Actualizar y borrar un registro de contáctenos</h2><hr v-if="admin">
        <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
          <table class="table table-sm table-bordered table-light" style="width: 100%">  
            <thead>
              <tr>
                <!-- <th scope="col">#</th> -->
                <th scope="col">Nombre</th>
                <th scope="col">Apellido</th>
                <th scope="col">Correo</th>
                <th scope="col">Asunto</th>
                <th scope="col">Mensaje</th>
                <th scope="col" v-if="admin">Acción</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in contactArray" :key="index">
                <!-- <th scope="row">{{item._id}}</th> -->
                <td>{{item.name}}</td>
                <td>{{item.lastName}}</td>
                <td>{{item.email}}</td>
                <td>{{item.subject}}</td>
                <td>{{item.message}}</td>
                <td v-if="admin">
                  <button type="button" class="btn btn-danger" @click="delContact(item._id)"><i class="far fa-trash-alt"></i></button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminCont',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  // if (adminAuth!=='ok') {
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      contactArray:[],
      contactObj:{name:"", lastName:"", email: "", subject:"", message:""},
    };
  },

    computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {
    this.listContact();
  },

  methods: {

    listContact(){
      this.axios.get('/g-contact')
      .then(res=>{
        this.contactArray=res.data;
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    delContact(id){
      console.log(id)
      this.axios.delete(`d-contact/${id}`) 
      .then(res => { 
        let index = this.contactArray.findIndex( item => item._id === res.data._id ) 
        this.contactArray.splice(index, 1); 
        this.message.text = 'Registro Eliminado!'; 
        this.message.color = 'danger'; 
        this.showAlert(); 
        }) 
        .catch( e => { 
          console.log(e.response); 
        })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }

  },
}
</script>

