<template>
  <div class="admin-dolexc">
    <HeaderComp></HeaderComp>
     <div class="container">
      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-secondary">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-secondary">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-secondary">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-light">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-secondary">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-secondary">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>      

      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>DÓLAR</h1>
          </div>
        </b-card>
      </div>

      <b-alert 
        :show="dismissCountDown" 
        dismissible 
        :variant="message.color" 
        @dismissed="dismissCountDown=0" 
        @dismiss-count-down="countDownChanged" > 
        {{message.text}} 
      </b-alert>
      
      <div class="editdol" v-if="edit">
        <h2>Editar un registro del Dólar</h2><hr>       
          <form @submit.prevent="editDollar(dollarEditObj)" v-if="edit">
            <input type="text" class="form-control my-2" placeholder="Fecha de vigencia" v-model="dollarEditObj.validdate">
            <input type="text" class="form-control my-2" placeholder="Tasa del día" v-model="dollarEditObj.dayrate">
            <b-button variant="outline-primary" type="submit">Editar</b-button>
            <b-button variant="outline-secondary" type="submit" @click="edit=false">Cancelar</b-button>
          </form>
      </div>

      <div class="adddol" v-if="!edit && admin"><hr>
        <h2>Adicionar un nuevo registro del dólar</h2><hr>
          <form @submit.prevent="addDollar()" v-if="!edit">
            <input type="text" class="form-control my-2" placeholder="Fecha de vigencia" v-model="dollarObj.validdate">
            <input type="text" class="form-control my-2" placeholder="Tasa del día" v-model="dollarObj.dayrate">
            <b-button variant="outline-success" type="submit">Adicionar</b-button>
          </form>
      </div>

      <div class="listdol"><hr v-if="admin">
        <h2 v-if="admin">Actualizar y borrar un registro del Dólar</h2><hr v-if="admin">
        <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
          <table class="table table-sm table-bordered table-light" style="width: 100%">  
            <thead>
              <tr>
                <!-- <th scope="col">#</th> -->
                <th scope="col">Fecha de vigencia</th>
                <th scope="col">Tasa del día</th>
                <th scope="col" v-if="admin">Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr tr v-for="(item, index) in dollarArray" :key="index">
                <!-- <th scope="row">{{item._id}}</th> -->
                <td>{{item.validdate}}</td>
                <td>{{item.dayrate}}</td>
                <td v-if="admin">
                  <button type="button" class="btn btn-success mx-1" @click="activateEdit(item._id)"><i class="fas fa-edit"></i></button>
                  <button type="button" class="btn btn-danger" @click="delDollar(item._id)"><i class="far fa-trash-alt"></i></button>
                </td>
              </tr>
            </tbody>
          </table>
          </div>
        </div>
      </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminDolExc',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  // if (adminAuth!=='ok') {
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      dollarArray:[],
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      dollarObj:{validyear:"", dayrate:""},
      edit:false,
      dollarEditObj:{}
    };
  },

  computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },


  created() {
    this.listDollar();
  },

  methods: {

    listDollar(){
      this.axios.get('/g-dollarex')
      .then(res=>{
        this.dollarArray=res.data;
        this.dollarArray.splice(0,250);
        this.dollarArray.reverse();
        // console.log(this.dollarArray);
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    addDollar(){
        this.axios.post('/p-dollarex',this.dollarObj)
        .then(res=>{

            this.dollarArray.push(res.data);
            this.dollarArray.reverse();
            this.dollarObj.validdate="";
            this.dollarObj.dayrate="";
            this.mensaje.color="success";
            this.mensaje.texto="Registro adicionado";
            this.showAlert();
        })
        .catch(e=>{
            console.log(e.response);
        })
    },

    activateEdit(id){
      this.edit=true;
      this.axios.get(`/g-dollarex/${id}`)
      .then(res=>{
          this.dollarEditObj=res.data;
      })
      .catch(e=>{
            console.log(e.response);
      })
    },

    editDollar(editObj){
        this.axios.put(`u-dollarex/${editObj._id}`, editObj)
      .then(res=>{
        const index= this.dollarArray.findIndex(item=> item._id===res.data._id);
        this.dollarArray[index].validdate=res.data.validdate;
        this.dollarArray[index].dayrate=res.data.dayrate;
        this.mensaje.color="success";
        this.mensaje.texto="Registro editado";
        this.showAlert();
        this.edit=false;
      })
      .catch(e=>{

          console.log(e.response);

      })
    },

    delDollar(id){
      console.log(id)
      this.axios.delete(`d-dollarex/${id}`) 
      .then(res => { 
        let index = this.dollarArray.findIndex( item => item._id === res.data._id ) 
        this.dollarArray.splice(index, 1); 
        this.message.text = 'Registro Eliminado!'; 
        this.message.color = 'danger'; 
        this.showAlert(); 

        }) 
        .catch( e => { 
          console.log(e.response); 
        })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }
  },
}
</script>

