<template>
  <div class="admin">
    <HeaderComp></HeaderComp>
    <div class="container">
      <b-container class="bv-example-row">
        <b-row>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/user"><b-button class="butuse text-light">Usuario</b-button></router-link>
                <router-link to="/admin/sus"><b-button class="butsus text-secondary">Suscriptor</b-button></router-link>
                <router-link to="/admin/pqr"><b-button class="butcon text-secondary">Contáctenos</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
          <b-col>
            <div class="mt-3">
              <b-button-group>
                <router-link to="/admin/dolexc"><b-button class="butdol text-secondary">Dólar</b-button></router-link>
                <router-link to="/admin/uvr"><b-button class="butuvr text-secondary">UVR</b-button></router-link>
                <router-link to="/admin/uvt"><b-button class="butuvt text-secondary">UVT</b-button></router-link>
              </b-button-group>
            </div>
          </b-col>
        </b-row>
      </b-container>  
      
      <div class="card">
        <b-card class="text-center" style="background:#212f59">
          <div class="bg-light" >
            <h1>USUARIO</h1>
          </div>
        </b-card>
      </div>

      <b-alert 
        :show="dismissCountDown" 
        dismissible 
        :variant="message.color" 
        @dismissed="dismissCountDown=0" 
        @dismiss-count-down="countDownChanged" > 
        {{message.text}} 
      </b-alert>


      <div class="edituser" v-if="edit"><hr>
        <h2>Editar un registro de Usuario</h2><hr>
        <form @submit.prevent="editUser(userEditObj)">
          <input type="text" class="form-control my-2" placeholder="Nombre" v-model="userEditObj.name">
          <input type="text" class="form-control my-2" placeholder="Apellido" v-model="userEditObj.lastName">
          <input type="email" class="form-control my-2" placeholder="Correo" v-model="userEditObj.email">
          <input type="text" class="form-control my-2" placeholder="Perfil" v-model="userEditObj.profile">
          <input type="password" class="form-control my-2" placeholder="Contraseña" v-model="userEditObj.password">
          <b-button variant="outline-primary" type="submit">Actualizar</b-button>
          <b-button variant="outline-secondary" type="submit" @click="edit=false">Cancelar</b-button>
        </form>
      </div>

      <div class="adduser" v-if="!edit && admin"><hr>
        <h2>Adicionar un nuevo registro de Usuario</h2><hr>
        <form @submit.prevent="addUser()">
          <input type="text" class="form-control my-2" placeholder="Nombre" v-model="userObj.name">
          <input type="text" class="form-control my-2" placeholder="Apellido" v-model="userObj.lastName">
          <input type="email" class="form-control my-2" placeholder="Correo" v-model="userObj.email">
          <input type="text" class="form-control my-2" placeholder="Perfil" v-model="userObj.profile">
          <input type="password" class="form-control my-2" placeholder="Contraseña" v-model="userObj.password">
          <b-button variant="outline-success" type="submit">Adicionar</b-button>
        </form>
      </div>

        <div class="listuser"><hr v-if="admin">
          <h2 v-if="admin">Actualizar y borrar un registro de usuario</h2><hr v-if="admin">
            <div class="table-responsive-sm table-wrapper-scroll-y my-custom-scrollbar">
              <table class="table table-sm table-bordered table-light" style="width: 100%">  
                <thead>
                  <tr>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                    <th scope="col">Correo</th>
                    <th scope="col">Perfil</th>
                    <th scope="col">Contraseña</th>
                    <th scope="col" v-if="admin">Acciones</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in userArray" :key="index">
                    <!-- <th scope="row">{{item._id}}</th> -->
                    <td>{{item.name}}</td>
                    <td>{{item.lastName}}</td>
                    <td>{{item.email}}</td>
                    <td>{{item.profile}}</td>
                    <td>{{item.password}}</td>
                    <td v-if="admin">
                      <button type="button" class="btn btn-success mx-1" @click="activateEdit(item._id)"><i class="fas fa-edit"></i></button>
                      <button type="button" class="btn btn-danger" @click="delUser(item._id)"><i class="far fa-trash-alt"></i></button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
      </div>
    <FooterComp></FooterComp>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComp from '@/components/HeaderComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'AdminUser',
  components: {
    HeaderComp,
    FooterComp
},

beforeCreate() {
  var adminAuth=window.localStorage.getItem('adminAuth');
  if (!adminAuth) {
    this.$router.push({path:'/admin/login'});
  }
},

  data() {
    return {
      message: {color: 'success', text: ''}, 
      dismissSecs: 5, 
      dismissCountDown: 0,
      userArray:[],
      userObj:{name:"", lastName:"", email: "", profile:"", password:""},
      userEditObj:{},
      edit:false,
    };
  },
  computed: {

    admin: function () {
      if (window.localStorage.getItem('admin')==="true") {
        return true;
      } else {
        return false;
      }
    }
  },

  created() {
    this.listUser();
  },

  methods: {

    listUser(){
      this.axios.get('/g-user')
      .then(res=>{
        this.userArray=res.data;
      })
      .catch(e=>{
        console.log(e.response);
      })
    },

    addUser(){
        this.axios.post('/p-user',this.userObj)
        .then(res=>{

            this.userArray.push(res.data);
            this.userObj.name="";
            this.userObj.lastName="";
            this.userObj.email="";
            this.userObj.profile="";
            this.userObj.password="";
            this.mensaje.color="success";
            this.mensaje.texto="Registro adicionado";
            this.showAlert();
        })
        .catch(e=>{
            console.log(e.response);
        })
    },

    activateEdit(id){
      this.edit=true;
      this.axios.get(`/g-user/${id}`)
      .then(res=>{
          this.userEditObj=res.data;
      })
      .catch(e=>{
            console.log(e.response);
      })
    },

    editUser(editObj){
        this.axios.put(`u-user/${editObj._id}`, editObj)
      .then(res=>{
        const index= this.userArray.findIndex(item=> item._id===res.data._id);
        this.userArray[index].name=res.data.name;
        this.userArray[index].lastName=res.data.lastName;
        this.userArray[index].email=res.data.email;
        this.userArray[index].profile=res.data.profile;
        this.userArray[index].password=res.data.password;
        this.mensaje.color="success";
        this.mensaje.texto="Registro editado";
        this.showAlert();
        this.edit=false;
      })
      .catch(e=>{
          console.log(e.response);
      })
    },

    delUser(id){
      this.axios.delete(`d-user/${id}`) 
      .then(res => { 
        let index = this.userArray.findIndex( item => item._id === res.data._id ) 
        this.userArray.splice(index, 1); 
        this.message.text = 'Registro Eliminado!'; 
        this.message.color = 'danger'; 
        this.showAlert(); 
        }) 
        .catch( e => { 
          console.log(e.response); 
        })
    },

    countDownChanged(dismissCountDown) { 
        this.dismissCountDown = dismissCountDown 
    }, 

    showAlert() { 
        this.dismissCountDown = this.dismissSecs 
    }
  },
}
</script>

<style scoped>

h2{
  color: #467694;
  text-align: center;
}

.mt-3 button{
  background: white;
  border: 2px solid #467694;
  width: 7em;
}

.mt-3 .butuse{
  background:#467694;
}

.card{
  margin-top: 15px;
  margin-bottom: 15px;
}

input{
  width: 20em;
}

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;}

table{
  margin-top: 40px;
  align-items: center;
  text-align: center;
  width: 30em;
}


table{
  margin-top: 40px;
}

table button{
  font-size: 1em;
}

.my-custom-scrollbar {
      position: relative;
      height: 200px;
      overflow: auto;
      }
      .table-wrapper-scroll-y {
      display: block;
}
.table-image 
  td, th {
    vertical-align: middle;
  
}

</style>
